import React,{useEffect} from 'react'
import {Card , Avatar } from 'antd'

import {useDispatch ,useSelector} from 'react-redux'
import { LOG_IN, loginAction } from '../reducers/user';


const UserProfile =() =>{
    
    const dispatch=useDispatch();
    const {userInfo} = useSelector(state=>state.user);

 
    useEffect(()=>{
        dispatch(loginAction);

    },[]) 

    return(
    <Card actions={[
        <div key="twit">짹짹<br />{userInfo.Post.length}</div>,
        <div key="following">팔로잉<br />{userInfo.Followings.length}</div>,
        <div key="follower">팔로워<br />{userInfo.Followers.length}</div>,

      ]}>                                   
<Card.Meta avatar={<Avatar>{userInfo.nickname[0]}</Avatar>} title={userInfo.nickname}/>
</Card>
)
}

export default UserProfile; 